@use "modules/core";
@use "modules/components/soul-skeleton";

:host {
  --_soul-thumbnail-size: #{core.$soul-space-unit-xl};
  display: block;
  width: var(--_soul-thumbnail-size);
  height: var(--_soul-thumbnail-size);
}

:host([size="s"]) {
  --_soul-thumbnail-size: #{core.$soul-space-unit-m};
}

:host([size="m"]) {
  --_soul-thumbnail-size: #{core.$soul-space-unit-xl};
}


:host([size="l"]) {
  --_soul-thumbnail-size: #{core.$soul-space-unit-2-xl};
}

:host([size="xl"]) {
  --_soul-thumbnail-size: #{2 * core.$soul-space-unit-2-xl};
}

.soul-skeleton-thumbnail {
  @include soul-skeleton.soul-skeleton-element();

  width: 100%;
  height: 100%;
  background-color: core.$soul-color-illustration-base;
}

.soul-skeleton-thumbnail--square {
  border-radius: core.$soul-border-radius-m;
}

.soul-skeleton-thumbnail--circle {
  border-radius: core.$soul-border-radius-circle;
}

@media screen and (prefers-reduced-motion: no-preference) {
  .soul-skeleton-thumbnail {
    @include soul-skeleton.soul-skeleton-shimmer-animation();
  }
}
