import {Component, Element, h, Host, Prop} from '@stencil/core';
import {HostElement, Watch} from '@stencil/core/internal';

@Component({
  tag: 'soul-skeleton-thumbnail',
  styleUrl: 'soul-skeleton-thumbnail.scss',
  shadow: true,
})
export class SoulSkeletonThumbnail {
  @Prop({reflect: true}) size: 's' | 'm' | 'l' | 'xl' = 'm';
  @Prop({reflect: true}) shape: 'circle' | 'square' = 'square';
  @Element() hostElement: HostElement;

  private supportedSizes = ['s', 'm', 'l', 'xl'];

  @Watch('size')
  public sizeChange(newValue: string): void {
    this.validateSize(newValue);
  }

  private validateSize(newValue: string): void {
    if (!this.supportedSizes.includes(newValue)) {
      console.debug(`Size ${newValue} not supported. Supported sizes: ${this.supportedSizes.join(', ')}`);
    }
  }

  render() {
    return (
      <Host>
        <div class={`soul-skeleton-thumbnail ${this.shape === 'circle' ? 'soul-skeleton-thumbnail--circle' : 'soul-skeleton-thumbnail--square'}`}></div>
      </Host>
    );
  }
}
